import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TraceService } from '../../../services/trace.service';

@Component({
  selector: 'app-btc',
  templateUrl: './btc.page.html',
  styleUrls: ['./btc.page.scss'],
})
export class BtcPage implements OnInit {
  hash: string;
  // BTCtransactions: object;
  data: object;
  backButtonText = '';

  constructor(
    private activatedRoute: ActivatedRoute,
    private traceService: TraceService
  ) { }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe(paramMap => {
      if(!paramMap.has('hash')) {
        // TODO: route to homepage / display error?
        return;
      }

      this.hash = paramMap.get('hash');
      this.fetchData(this.hash);
    })
  }

  fetchData(hash: string) {
    this.traceService.fetchTransaction(hash).toPromise().then(res => {
      console.log(res);
      this.data = res;
    });
  }
}
